/* eslint-disable  */

import { ValidationObserver, ValidationProvider, extend, setInteractionMode } from 'vee-validate'
import{ email, length, max, max_value, min, min_value, numeric, required } from 'vee-validate/dist/rules'
import Vue from 'vue'

extend('email', {
  ...email,
  message: 'O campo {_field_} no  um email vlido'
})
extend('length', {
  ...length,
  message: 'O campo {_field_} deve ter {length} caracteres'
})
extend('numeric', {
  ...numeric,
  message: 'O campo {_field_} requer somente nmeros'
})
extend('required', {
  ...required,
  message: 'O campo {_field_}  obrigatrio'
})

extend('max', {
  ...max,
  message: (campo, valor) => {
    return `Valor mximo para ${campo} : ${valor.length}`
  }
})

extend('min', {
  ...min,
  message: (campo, valor) => {
    return `Valor mnimo para ${campo} : ${valor.length}`
  }
})

extend('max_value', {
  ...max_value,
  message: 'Valor mximo para {_field_} : {max}'
})

extend('min_value', {
  ...min_value,
  message: 'Valor mnimo para {_field_} : {min}'
})


setInteractionMode('passive')

Vue.component('ValidationProvider', ValidationProvider)
Vue.component('ValidationObserver', ValidationObserver)